.root{width:250px;display:flex;flex-direction:column;gap:10px}.textInput{padding:10px 15px;width:100%;border-radius:5px;border:none}.textInput:disabled{color:#707070;background-color:#dcdcdc;opacity:.8;pointer-events:none}.textInput:-webkit-autofill:disabled{-webkit-text-fill-color:#707070 !important;-webkit-box-shadow:0 0 0 30px #dcdcdc inset !important;box-shadow:0 0 0 30px #dcdcdc inset !important;opacity:.8;pointer-events:none}.validateError{color:#ac1919;font-size:1.6rem}.validateError::before{display:inline;content:"⚠ "}